  <template>
<div>
  <a    class="fas fa-angle-up" id="scroll-top"> up </a>

</div>
</template>
<script>
export default {
data: function () {
return {
 };
},
mounted: function() {
 window.onscroll = () => {
           document.querySelector("#scroll-top")

    
    if (window.scrollY > 1400) {
        document.querySelector("#scroll-top").classList.add("active")
    }
    else {
        document.querySelector("#scroll-top").classList.remove("active")


    }
    
 
 }
 document.querySelector("#scroll-top").onclick = function (){-
     window.scrollTo({
         top :0 ,
         behavior:"smooth"
     })
 }
 
}
};
</script>
<style scoped>

#scroll-top {
    position: fixed;
    bottom: 10px;
    right: -100px;
     padding: 10px;
    font-size: 12px;
    background-color: var(--red);
    color: #fff;
    border-radius: 5px;
    transition: 1s linear;
    background: #0dcaf0;
    z-index: 1000;
    font-weight: bold;
    cursor: pointer;
}

#scroll-top.active {
 right: 10px;
 
 }
</style>