<template>
    <div class="hello  "> 
        <div class="container col-md-4 ">
        
            <form action method="post" @submit.prevent="SubmitForm()">
                <div class="  from-group text-start">
                    <label>
                        Full Name
                        <span class="text-danger">*</span>
                    </label>
                    <input
                        type="text"
                        class="form-control"
                        v-model.trim="$v.fullname.$model"
                        :class="{
                            'is-invalid': $v.fullname.$error,
                            'is-valid': !$v.fullname.$invalid,
                        }"
                    />
                    <div class="valid-feedback">Your Full Name Is valid !</div>
                    <div class="invalid-feedback">
                        <span v-if="!$v.fullname.required"
                            >Full Name Is Requird</span
                        >
                        <span v-if="!$v.fullname.minLength">
                            Full Name must have at least
                            {{ $v.fullname.$params.minLength.min }} letters.
                        </span>
                        <span v-if="!$v.fullname.maxLength">
                            Full Name must have at most
                            {{ $v.fullname.$params.maxLength.max }} letters.
                        </span>
                    </div>
                </div>
                
 

                <div class="  from-group  text-start">
                    <label>
                        password
                        <span class="text-danger">*</span>
                    </label>
                    <input
                        type="password"
                        id="password"
                        class="form-control"
                        v-model.trim="$v.password.$model"
                        :class="{
                            'is-invalid': $v.password.$error,
                            'is-valid': !$v.password.$invalid,
                        }"
                    />
                    <div class="valid-feedback">Your password Is valid !</div>
                    <div class="invalid-feedback">
                        <span v-if="!$v.password.required"
                            >password Is Requird</span
                        >
                        <span v-if="!$v.password.minLength">
                            {{ $v.password.$params.minLength.min }}
                            characters minimum
                        </span>
                    </div>
                </div>
                <div class=" from-group  form-check text-start">
                    <input
                        type="checkbox"
                        id="showpassword"
                        class="form-check-input"
                        @click="toggleshowpassword"
                        v-model="showpassword"
                    />
                    <label class="form-check-label" for="showpassword"
                        >show password</label
                    >
                </div>
                <div class=" from-group text-start">
                    <label>
                        repeat password
                        <span class="text-danger">*</span>
                    </label>
                    <input
                        type="password"
                        class="form-control"
                        v-model.trim="$v.repeatpassword.$model"
                        :class="{
                            'is-invalid': $v.repeatpassword.$error,
                            'is-valid':
                                password != '' ? !$v.password.$invalid : '',
                        }"
                    />
                    <div class="valid-feedback">
                        Your repeatpassword Is identical !
                    </div>
                    <div class="invalid-feedback">
                        <span v-if="!$v.repeatpassword.sameAspassord"
                            >password must be identical.</span
                        >
                    </div>
                </div>
                <div class="  from-group text-start">
                    <label>
                        phone Number
                        <span class="text-danger">*</span>
                    </label>
                    <input
                        type="number"
                        class="form-control"
                        v-model.trim="$v.phone.$model"
                        :class="{
                            'is-invalid': $v.phone.$error,
                            'is-valid': !$v.phone.$invalid,
                        }"
                    />
                    <div class="valid-feedback">
                        Your phone Numbe Is valid !
                    </div>
                    <div class="invalid-feedback">
                        <span v-if="!$v.phone.required"
                            >phone Number is required</span
                        >
                        <span v-if="!$v.phone.numeric"
                            >this phone Number only numeric accepted</span
                        >
                    </div>
                     </div>
           
           
 
                <div class="send-button">
                    <button type="submit" class="btn btn-primary">
                        SUBMIT {{ Submitstatus }}
                    </button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import {
    required,
    minLength,
    maxLength,
    sameAs,
    numeric,
 }
  from "vuelidate/lib/validators";
export default {
    name: "HelloWorld",

    data: function () {
        return {
 
            fullname: "",
             password: "",
            repeatpassword: "",
            showpassword: false,
            phone: "",
             Submitstatus:  null,
        };
    },
    validations: {
        fullname: {
            required,
            minLength: minLength(3),
            maxLength: maxLength(20),
        },
         
        password: {
            required,
            minLength: minLength(8)
         },
        repeatpassword: {
            sameAspassord: sameAs("password"),
        },
        phone: {
            required,
            numeric,
            minLength: minLength(11),
        },
         
    },
    methods: {
        toggleshowpassword() {
            var show = document.getElementById("password");
            if (this.showpassword == false) {
                this.showpassword = true;
                show.type = "text";
            } else {
                this.showpassword = false;
                show.type = "password";
            }
        },
        SubmitForm() {
            this.$v.$touch()
               
            if (this.$v.$invalid) {
                this.Submitstatus = "FATL";
            } else {
                this.Submitstatus = "success" 
                this.$router.push("/hp")
              
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.hello {
    box-sizing: border-box;
    font-size: 18px;
    .container{
        text-align: center;
 
    }
    
 
        form {
            width: 100%;
            margin: 20px auto;
            .from-group {
                margin-bottom: 20px ;
                 label {
                    display: block;
                    margin-bottom: 15px;
                }
                .form-control {
                      background-color: #efefef;
                    border: 1px solid #ccc;
                    width: 100%;
                }
           
        }}
        
    
}
</style>
