<template>
    <div class="DEL mx-1">
        <div class="container-fluid">
            <div class="row">
                <laptopPtoductVue
           v-for="(product ,index) in productsPerPage"
                    :key="index + Math.random()"
                    :title="product.title ? product.title : 'title'"
                    :index="index"
                    :price="product.price ? product.price : '21'"
                    :p="product.p ? product.p : 'ds'"
                    :p1="product.p1 ? product.p1 : 'ds'"
                    :p2="product.p2 ? product.p2 : 'dssss'"
                    :p3="product.p3 ? product.p3 : 'dsds'"
                    :link="product.link ? product.link : 'ds'"
                    :amount="product.amount ? product.amount : 'ds'"
                                     :img="product.img ? product.img : '270-xps-13-italia-9305-hi-res-imagery-notebook-xps-13-9305-4000x4000.webp'"

                ></laptopPtoductVue>
                <nav aria-label="Page navigation example">
                    <ul class="pagination justify-content-center">
                        <li class="page-item" @click="Previous">
                            <a class="page-link">Previous</a>
                        </li>
                        <li
                            v-for="(item, index) in buttons"
                            class="page-item"
                            :key="index"
                            :class="{ active: item.isActive }"
                            @click="
                                loadData(index * 12, (index + 1) * 12, index)
                            "
                        >
                            <a class="page-link">{{ index + 1 }}</a>
                        </li>

                        <li class="page-item" @click="next">
                            <a class="page-link">Next</a>
                        </li>
                    </ul>
                </nav>
            </div>
        </div>
    </div>
</template>

<script>
import laptopPtoductVue from "../components/laptop/lapoptPtoduct.vue";

import jsonproduct from "../assets/dp.json/dp1.json";
export default {
    data: function () {
        return {
            products: [],
            perPage: 12,
            buttons: [],
            productsPerPage: [],
            start: 0,
            end: 0,
            isActive: false,
        };
    },
    name: "DEL",
    components: {
        laptopPtoductVue,
    },
    methods: {
        loadData(start, end, index = 0) {
 
            this.productsPerPage = this.products.slice(start, end);
            console.log(this.productsPerPage.length)
            this.start = start;
            this.end = end;
            
                
        
            this.buttons = this.buttons.map((el, i) => {
                if (i == index) {
                    return { isActive: true };
                } else {
                    return { isActive: false };
                }
                
            });
            window.scrollTo(0 ,0)

        },

        next() {
            if (this.end == 72) {
                return;
            } else {
                this.end = this.end + 12;
                this.start = this.end == 12 ? 0 : this.start + 12;
                this.loadData(this.start, this.end,this.start ==0 ? 0 : this.start /12);
             }
        },

        Previous() {
            if (this.start == 0) {
                return;
            } else {
                this.start = this.start - 12;
                this.end = this.end - 12;

                this.loadData(this.start, this.end , this.start / 12 );
            }
        },
    },

    created() {
        this.products = jsonproduct;
        this.productsPerPage = this.products.slice(0, 12);
        this.buttons = Array.from({ length: 6 }).fill({ isActive: false });
        this.buttons = this.buttons.map((ele, i) => {
            if (i == 0) {
                return { isActive: true };
            } else {
                return ele;
            }
        });
    },
};
</script>
<style scoped>
.about {
    padding: 0;
}
a{
    border-radius: 5px;
}
li {
        margin-left:10px  ;
    }

</style>
