import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Del from "../views/Del.vue";
import Hp from "../views/Hp.vue";

Vue.use(VueRouter);
const routes = [
    {
        path: "/",
        redirect: "/home",
    },
    {
        path: "/DEL",
        name: "DEL",
        component: Del,
    },
    {
        path: "/HP",
        name: "Hp",
        component: Hp,
    },

    {
        path: "/home",
        name: "Home",
        component: Home,
    },
];
const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes,

    scrollBehavior(to, from, savedPosition) {
        return {
            x: 0,
            y: 0,
            behavior: "smooth",
        };
    },
});

export default router;
