<template>
    <div class="laptop-Product col-sm-12 col-md-6 col-xl-4 col-xxl-3 px-1">
        <div class="product">
            <a :href="link">
                <img :src="imageLink" class="card-img-top" />
            </a>
            <div class="card-body">
                <h3 class="card-title">
                    <a :href="link">{{ this.title }}</a>
                </h3>

                <ul class="ps-0">
                    <li>
                        <span class="icon">*</span>

                        <span>{{ p }}</span>
                    </li>
                    <li>
                        <span class="icon">*</span>

                        <span>{{ p1 }}</span>
                    </li>
                    <li>
                        <span class="icon">*</span>

                        <span>{{ p2 }}</span>
                    </li>

                    <li>
                        <span class="icon">*</span>

                        <span>{{ p3 }}</span>
                    </li>
                </ul>
                <div class="cont">
                    <span class="amount-title">Amount: {{ Amount }}</span>
                    <span class="price">price: ${{ price }}</span>

                    <input
                        id="input-range"
                        type="range"
                        min="1"
                        max="100"
                        step="1"
                        v-model="Amount"
                        clas="form-check-input+ "
                    />
                    <input class="input-text" type="text" v-model="Amount" />
                </div>
                <div class="prices">
                    <button
                        class="totle-price"
                        @click="showInput"
                        v-if="!hideInput"
                    >
                        Total
                    </button>
                    <div class="total" v-if="hideInput">
                        Total = $ {{ totalprice }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ["img", "title", "amount", "price", "p", "p1", "p2", "p3", "link"],
    data: function () {
        return {
            Amount: 0,
            Price: "price",
            hideInput: false,
        };
    },
    computed: {
        totalprice: function () {
            let tot=   this.price * this.Amount ;
            if (this.Amount >= 3){
                tot =tot-50
             return tot.toLocaleString();
                }else{
                                return tot.toLocaleString();

            }
        },
        imageLink() {
            if (this.img.search("http") == 0) {
                return this.img;
            } else {
                return require("@/assets/images/" + this.img);
            }
        },
    },
    methods: {
        showInput() {
            this.hideInput = true;
        },
    },
    name: "laptop-Product",
};
</script>

<style lang="scss">
* {
    text-transform: capitalize;
    transition: all 0.2s linear;
}

.product {
    width: 100%;
    height: 690px;
    background-color: white;
    padding: 20px;
    position: relative;
    margin-bottom: 10px;
    border: 1px solid transparent;
    border-radius: 3px;
}

.product:hover {
    border: 1px solid #898888;
}

img {
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
    overflow: hidden;
    border-radius: 5px;
}
.product:hover img {
    transform: scale(1.1);
}
h3 {
    font-size: 18px;
}
h3 a:hover {
    color: #0076ce;
    border-bottom: 1px solid #0076ce;
}

a {
    text-decoration: none;
    cursor: pointer;
    padding: 0;
}

ul {
    width: 100%;
    margin: 5px 0 20px 0;
    font-size: 15px;
    line-height: 1.8;
    padding: 15px 0;
}

li {
    list-style: none;
    font-size: 14px;
    line-height: 24px;
    margin: 5px 0;
    max-width: 100%;
}
ul li span {
    white-space: wrap;
}

.cont {
    position: absolute;
    left: 0;
    bottom: 75px;
    margin: 0 5px;
}
.icon {
    padding: 5px;
    color: #0076ce;
}

.amount-title {
    float: left;
    margin-bottom: 20px;
    margin-left: 5px;
}
.price {
    float: right;
    margin-bottom: 20px;
    margin-right: 5px;
}

#input-range {
    width: 100%;
    border-radius: 5px;
    margin-bottom: 20px;
}
.input-text {
    width: 100%;
    padding: 2px 20px;
    border-radius: 5px;
    margin-bottom: 20px;
    background-color: #eee;
}

.totle-price {
    margin: 20px;
    padding: 10px;
    background-color: white;
    border: 1px solid black;
    color: black;
    text-align: center;
    text-transform: uppercase;
    position: absolute;
    left: 5px;
    bottom: 5px;
}
.totle-price:hover {
    background-color: #0096d6;
    color: #fff;
    border: none;
}
.total {
    margin-top: 20px;
    text-transform: uppercase;
    color: #0171ad;

    text-align: center;
    position: absolute;
    left: 30px;
    bottom: 30px;
}
</style>
