<template>
    <div class="HP mx-1">
        <div class="container-fluid">
            <div class="row">
                <AboutPtoductVue
                    v-for="(product ,index) in productsPerPage"
                    :key="index + Math.random()"
                    :title="product.title "
                    :index="index"
                    :price="product.price "
                    :p="product.p "
                    :p1="product.p1 "
                    :p2="product.p2"
                    :p3="product.p3 "
                    :link="product.link "
                    :amount="product.amount "
                    :img="product.img "
                ></AboutPtoductVue>
                <nav aria-label="Page navigation example">
                    <ul class="pagination justify-content-center  ">
                        <li class="page-item " @click="Previous">
                            <a class="page-link Previous">Previous</a>
                        </li>
                        <li
                            v-for="(item, index) in buttons"
                            class="page-item"
                            :key="index"
                            :class="{ active: item.isActive }"
                            @click="
                                loadData(index * 12, (index + 1) * 12, index)
                            "
                        >
                            <a class="page-link">{{ index + 1 }}</a>
                        </li>

                        <li class="page-item " @click="next">
                            <a class="page-link Next">Next</a>
                        </li>
                    </ul>
                </nav>
            </div>
        </div>
    </div>
</template>

<script>
import AboutPtoductVue from "../components/About/AboutPtoduct.vue";

import jsonproduct from "../assets/dp.json/dp.json";
export default {
    data: function () {
        return {
            products: [],
            perPage: 12,
            buttons: [],
            productsPerPage: [],
            start: 0,
            end: 0,
            isActive: false,
        };
    },
    name: "HP",
    components: {
        AboutPtoductVue,
    },
    methods: {
        loadData(start, end, index = 0) {
            
            this.productsPerPage = this.products.slice(start, end);
            this.start = start;
            this.end = end;
            this.buttons = this.buttons.map((el, i) => {
                if (i == index) {
                    return { isActive: true };
                } else {
                    return { isActive: false };
                }
            })
            window.scrollTo(0 ,0)


         },

        next() {
            if (this.end == 156) {
                return;
            } else {
                this.end = this.end + 12;
                this.start = this.end == 12 ? 0 : this.start + 12;
                this.loadData(this.start, this.end , this.start ==0 ? 0 : this.start /12);
             }
        },

        Previous() {
            if (this.start == 0) {
                return;
            } else {
                this.start = this.start - 12;
                this.end = this.end - 12;
                 
                this.loadData(this.start  , this.end , this.start / 12 );
            }
        },
    },

    created() {
        this.products = jsonproduct;
        this.productsPerPage = this.products.slice(0, 12);
       this.start = 0
    this.end = 12
this.buttons = Array.from({ length: 13 }).fill({ isActive: false });
        this.buttons = this.buttons.map((ele, i) => {
            if (i == 0) {
                return { isActive: true };
            } else {
                return ele;
            }
        });
    },
};
</script>
<style scoped>
.about {
    padding: 0;
}
li {
        margin-left:5px  ;
    }
    
a {
    border-radius: 50%;
}
.Next,
.Previous {
    border-radius: 50px;
    
}
@media (max-width : 720px){
    .pagination {
        display: flex;
        flex-wrap: wrap;
        flex:  1 1 20px;
        justify-content: space-between;
   

    }
    .Next,
.Previous {
    border-radius: 15px;
    
}
}
     
</style>
